.container {
    display: flex;
    /* height: 490px; */
    /* width: 1280px; */
    justify-content: center;

    margin: 3% 0 0 3%;
    padding: 0% 0.1%;
    /* margin-top: 3%;
   
     padding: 0px 15px; */
    max-width: 1280px;
    gap: 3%;
}

.div1 {
    flex: 50%;
    display: flex;
    max-width: 540px;
    /* height: 490px; */
    background-image: url("./images/imgMain1.png");
    background-repeat: no-repeat;
    background-position: center;
}

.bannerDiv {
    display: flex;
    width: 540px;
    height: 490px;
    justify-content: space-around;
    align-items: center;


}

.div1_1,
.div1_2 {
    display: flex;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #FFFFFF;
    text-align: center;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
}

.banner {
    display: flex;
    width: 289px;
    height: 100px;
    background: #FFFFFF;
    border-radius: 4px;
    text-align: center;
    vertical-align: middle;
    flex-direction: column;
    justify-content: space-evenly;

}

.p1 {
    font-family: Spartan;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 13px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #121212;

    opacity: 0.4;
    text-align: center;
    vertical-align: middle;
}


.p2 {
    font-family: Spartan;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    align-items: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #121212;
    text-align: center;
    vertical-align: middle;
}

.div2 {
    flex: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: fit-content;
}

.div2_1 {
    /* flex: auto; */
    display: flex;
    gap: 5%;
}

.wom,
.men {
    display: flex;
    width: 255px;
    height: 200px;
    justify-content: space-around;
    align-items: center;
}

.wom {
    background-image: url("./images/imgMain2.png");
    background-position: center;

}

.men {
    background-image: url("./images/imgMain3.png");
    background-position: center;
    /* margin-left: 5%; */
}

.bannerS {
    /* position: absolute; */
    display: flex;
    height: 48px;
    width: 118px;
    background-color: #FFFFFF;
    border-radius: 4px;
    justify-content: space-around;
    align-items: center;

}

.div2_2 {
    display: flex;
    background-image: url("./images/imgMain4.png");
    background-position: center;
    /* width: 540px; */
    height: 260px;
    background-repeat: no-repeat;
    justify-content: space-around;
    align-items: center;

}

.p3 {
    font-family: Spartan;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    /* identical to box height, or 157% */

    /* display: flex; */
    align-items: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;

    /* dark */

    color: #121212;
    text-align: center;
    vertical-align: middle;

}

.access {
    display: flex;
    width: 166px;
    height: 48px;
    background-color: #FFFFFF;
    justify-content: space-around;
    align-items: center;

}

.textAfter {
    display: flex;
    margin-top: 3%;
    justify-content: space-between;
    /* justify-content: space-around; */
    margin: 3% 0 0 3%;
    padding: 0% 0.1%;
    max-width: 1280px;

}

.imgBefore {
    display: flex;
    margin: 0 3% 0 3%;
    align-items: center;

}

.imgBefore img {
    /* margin-right: 10px; */

}

.divWidth {
    width: 302px;
    white-space: nowrap;
}