@media (min-width: 768px) and (max-width: 979px) {
    .container3 {
        /* max-width: 870px; */
        align-items:flex-end;    }

    .block6 {
        justify-content: space-around;
        flex-direction: column;
    }
    .div3{
        flex: 50%;
    }
    .div4{
        flex: 50%;
    }
}

@media (min-width: 450px) and (max-width: 767px) {
    .container3 {
        /* max-width: 620px; */

    }

    .block6 {
        justify-content: space-around;
        flex-direction: column;
        align-items:baseline ;
        align-content: stretch;

    }
}

@media (max-width:449px) {
    .container3 {
        /* max-width: 360px; */

    }

    .block6 {
        justify-content: space-around;
        flex-direction: column;

    }
}

.container3 {
    margin-top: 5%;
    display: flex;
    /* justify-content: space-between; */
    /* width: 1160px; */
    align-items: stretch;
    flex-direction: column;
    margin: 3% 0 0 3%;
    padding: 0% 0.1%;
    max-width: 1280px;
    align-items:stretch;
}

.box1 {
    display: flex;
    width: 90%;
}

.box2 {
    display: flex;
    width: 90%;
    justify-content: space-around;
    max-width: fit-content;

}

.div1 {
    flex: 1;
    display: flex;
    font-family: Spartan;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 25px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #121212;
    width: 90%;
}

.div2 {

    display: flex;
    font-family: Spartan;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 13px;
    letter-spacing: 0.06em;
    text-align: right;
    width: 10%;
}

.div2 div {}

.block6 {
    display: flex;
    margin-top: 4%;
    /* justify-content: space-between; */
    max-width: 1180px;
}



