@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap');

.container {
    
    display: flex;
    min-height: 24px;
    background-color: #121212;
    color: #FFFFFF;
    justify-content: space-around;
    align-items: center;
    font-family: Spartan;
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 12px;
       margin: 0% 0 0 0%;
       padding: 0% 2%;
    max-width: 1280px;

}



 /* @media ------------------------------- */


@media (min-width: 768px) and (max-width: 979px) {
    .container{
    /* max-width: 870px; */
    align-items: center;

}
}
@media (min-width: 441px) and (max-width: 767px)  {
    .container{
        /* max-width: 620px; */
    }
}
@media (max-width:440px) {
    .container {
        min-width: 440px;
        flex-direction: column;

        /* max-width:360px; */
    }
}


